interface CreateWebComponentProps {
  tagName: string;
  template?: string;
  style?: string;
  observedAttributes?: string[];
  methods?: {
    connectedCallback?: (this: HTMLElement) => void;
    disconnectedCallback?: (this: HTMLElement) => void;
    attributeChangedCallback?: (
      this: HTMLElement,
      name: string,
      oldValue: string | null,
      newValue: string | null
    ) => void;
  };
}

export function createWebComponent(
  config: CreateWebComponentProps
): CustomElementConstructor {
  const { tagName, template, style, observedAttributes, methods } = config;

  class GenericComponent extends HTMLElement {
    constructor() {
      super();
      this.attachShadow({ mode: "open" });

      if (style) {
        const styleEl = document.createElement("style");
        styleEl.textContent = style;
        this.shadowRoot!.appendChild(styleEl);
      }

      if (template) {
        const templateEl = document.createElement("template");
        templateEl.innerHTML = template;
        this.shadowRoot!.appendChild(templateEl.content.cloneNode(true));
      }
    }

    connectedCallback() {
      methods?.connectedCallback?.call(this);
    }

    disconnectedCallback() {
      methods?.disconnectedCallback?.call(this);
    }

    attributeChangedCallback(
      name: string,
      oldValue: string | null,
      newValue: string | null
    ) {
      methods?.attributeChangedCallback?.call(this, name, oldValue, newValue);
    }

    static get observedAttributes() {
      return observedAttributes || [];
    }
  }

  customElements.define(tagName, GenericComponent);
  return GenericComponent;
}
